.card {
	&-round-icon-and-text {
		width: 100%;
		background-color: $purple-faded;
		padding: 24px;
		color: $secondary;
		margin-bottom: 20px;
		border-radius: 20px;
		border: 1px solid rgba($primary, 0.20);
		transition: background-color 0.3s, border-color 0.3s;

		@include media-breakpoint-up(lg) {
			padding: 32px;
		}

		@include media-breakpoint-up(xxl) {
			padding: 40px;
		}

		&:hover {
			background-color: $primary;
			border-color: $primary;
			color: $white;

			.profile-item__text {
				color: rgba($white, 0.8);
			}
		}

		&.horizontal {
			position: relative;
			padding: 24px 24px 24px 90px;

			@include media-breakpoint-down(sm) {
				border-bottom: 1px solid $white !important;
			}

			@include media-breakpoint-up(sm) {
				padding: 24px 40px;
			}

			@include media-breakpoint-up(xl) {
				display: flex;
				align-items: center;
				padding: 35px 35px 35px 120px;
			}

			@include media-breakpoint-up(xxl) {
				padding-left: 150px;
			}

			.card-round-icon-and-text__icon {
				@include media-breakpoint-down(sm) {
					position: absolute;
					top: 50%;
					left: 24px;
					transform: translate(0, -50%);
					z-index: 5;
					margin-bottom: 0;
				}

				@include media-breakpoint-up(xl) {
					position: absolute;
					top: 50%;
					left: 35px;
					transform: translate(0, -50%);
					z-index: 5;
					margin-bottom: 0;
				}
			}
		}

		&__icon {
			@include sizeElement(48px);
			@include inlineFlexCenter;

			position: relative;
			overflow: hidden;
			margin-bottom: 20px;
			border-radius: 50%;
			background-color: $white;

			@include media-breakpoint-up(sm) {
				@include sizeElement(60px);
			}

			@include media-breakpoint-up(xxl) {
				@include sizeElement(80px);

				margin-bottom: 25px;
			}
		}

		&__icon-holder {
			@include sizeElement(24px);

			position: relative;

			@include media-breakpoint-up(sm) {
				@include sizeElement(32px);
			}

			@include media-breakpoint-up(xxl) {
				@include sizeElement(40px);
			}
		}

		&__title {
			margin-bottom: 0 !important;

			+ .card-round-icon-and-text__text {
				padding-top: 10px;
			}
		}
	}

	&-round-icon-and-title {
		background-color: $yellow;
		text-align: center;
		border-radius: 20px;
		padding: 25px 15px;
		width: 100%;
		margin-bottom: 20px;
		transition: background-color 0.3s;

		@include media-breakpoint-up(xl) {
			margin-bottom: 35px;
		}

		@include media-breakpoint-up(xxl) {
			padding: 25px;
		}

		&:hover {
			background-color: darken($yellow, 15%);

			.card-round-icon-and-title__icon {
				transform: scale(0.8);
			}
		}

		&__icon {
			@include sizeElement(48px);
			@include inlineFlexCenter;

			border-radius: 50%;
			background-color: $white;
			margin-bottom: 25px;
			transition: transform 0.3s;

			@include media-breakpoint-up(sm) {
				@include sizeElement(60px);
			}

			@include media-breakpoint-up(xxl) {
				@include sizeElement(80px);
			}
		}

		&__icon-holder {
			@include sizeElement(24px);

			position: relative;

			@include media-breakpoint-up(sm) {
				@include sizeElement(32px);
			}

			@include media-breakpoint-up(xxl) {
				@include sizeElement(40px);
			}
		}
	}

	&-team {
		margin-bottom: 20px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}

		&:hover {
			.card-team__photo {
				background-color: $primary;
			}
		}

		&__photo {
			@include responsiveRatio(1, 1);

			position: relative;
			overflow: hidden;
			border-radius: 20px;
			background-color: $yellow;
			transition: background-color 0.3s;
		}

		&__body {
			padding-top: 15px;

			@include media-breakpoint-up(lg) {
				padding-top: 20px;
			}
		}
	}

	&-why-us {
		display: flex;
		flex-direction: column;
		border-radius: 20px;
		background-color: $purple-faded;
		transition: color 0.3s, background-color 0.3s;
		width: 100%;
		margin-bottom: 20px;
		overflow: hidden;

		&:hover {
			color: $white;
			background-color: $primary;
		}

		&__img {
			@include responsiveRatio(680, 240);

			position: relative;
			overflow: hidden;
		}

		&__body {
			padding: 25px;
			flex-grow: 1;

			@include media-breakpoint-up(xxl) {
				padding: 40px;
			}
		}
	}

	&-metric {
		border-radius: 20px;
		padding: 30px;
		text-align: center;
		margin-bottom: 30px;
		width: 100%;

		@include media-breakpoint-up(lg) {
			margin-bottom: 80px;
		}

		@include media-breakpoint-up(xxl) {
			width: 360px;
			padding: 50px 40px 30px;
		}

		&__img {
			@include responsiveRatio(1, 1);

			position: relative;
			margin-bottom: 30px;
		}

		&__value {
			position: absolute;
			top: 50%;
			left: 0;
			right: 0;
			transform: translate(0, -50%);
			width: 100%;
			z-index: 5;

			@include media-breakpoint-up(lg) {
				font-size: 40px;
			}
		}
	}

	&-review {
		background-color: $purple-faded;
		border: 1px solid rgba($primary, 0.2);
		border-radius: 20px;
		width: 100%;
		padding: 20px;
		margin-bottom: 30px;
		transition: background-color 0.3s, border-color 0.3s;

		@include media-breakpoint-up(md) {
			padding: 40px 40px 70px 40px;
		}

		&:hover {
			background-color: darken($purple-faded, 2%);
			border-color: $primary;
		}

		&__header {
			margin-bottom: 30px;
		}

		&__logo {
			@include responsiveRatio(1, 1);

			position: relative;
			width: 70px;
			border-radius: 8px;
			background-color: $white;
			overflow: hidden;

			@include media-breakpoint-up(md) {
				width: 100px;
			}
		}

		&__review-text {
			position: relative;
			margin-bottom: 0;
			padding-top: 30px;

			@include media-breakpoint-up(xs) {
				padding-left: 90px;
				padding-top: 0;
			}

			@include media-breakpoint-up(md) {
				padding-left: 132px;
			}

			&:before {
				content: "\e90e";
				font-family: icomoon;
				position: absolute;
				top: 0;
				left: 0;
				color: $primary;
				font-size: 18px;

				@include media-breakpoint-up(xs) {
					left: 40px;
					font-size: 24px;
				}

				@include media-breakpoint-up(md) {
					left: 70px;
				}
			}
		}
	}
}

.cards-2-x-3 {
	.col,
	[class^="col"] {
		&:nth-child(5n + 1),
		&:nth-child(5n + 2) {
			@include media-breakpoint-up(xl) {
				width: 50%;
			}
		}
	}
}

.without-border-card {
	.card-round-icon-and-text {
		border: none;
	}
}

.small-title-card {
	.card-round-icon-and-text {
		&__title {
			@extend .h4;
		}
	}
}

.cards-metric-row {
	> .col,
	> [class^="col-"] {
		&:nth-child(3n + 1) {
			.card-metric {
				background-color: $purple-light;
				color: $white;
			}
		}

		&:nth-child(3n + 2) {
			.card-metric {
				background-color: $yellow;
			}
		}

		&:nth-child(3n + 3) {
			.card-metric {
				background-color: $white;
			}
		}
	}
}