.section {
	padding: 40px 0;

	@include media-breakpoint-up(lg) {
		padding: 60px 0;
	}

	@include media-breakpoint-up(xxl) {
		padding: 80px 0;
	}

	&.full-vh-height {
		display: flex;
		align-items: center;
		min-height: 100vh;
	}

	&-content-and-side-img {
		padding-top: 0;

		&.reversed {
			.section-content-and-side-img__content-row {
				@include media-breakpoint-up(xl) {
					flex-direction: row-reverse;
					justify-content: flex-end;
				}
			}

			.section-content-and-side-img__text-col {
				@include media-breakpoint-up(xl) {
					margin-left: 0 !important;
					margin-right: 8.33333333% !important;
				}
			}

			.section-content-and-side-img__img-holder {
				right: auto;
				left: 0;
			}

			.section-content-and-side-img__img {
				border-top-right-radius: 0;
				border-top-left-radius: 20px;
			}
		}

		+ .section-banner.bg-sm-purple-faded {
			position: relative;

			@include media-breakpoint-down(sm) {
				margin-top: -82px;
				padding-bottom: 0;
			}

			&:before {
				@include media-breakpoint-up(sm) {
					background-color: $purple-faded !important;
					content: "";
					display: block;
					position: absolute;
					bottom: 100%;
					left: 0;
					right: 0;
					height: 40px;
					pointer-events: none;
				}

				@include media-breakpoint-up(lg) {
					height: 60px;
				}

				@include media-breakpoint-up(xxl) {
					height: 80px;
				}
			}
		}

		&__content {
			background-color: $purple-faded;
			padding-top: 40px;

			@include media-breakpoint-up(md) {
				padding-top: 60px;
			}

			@include media-breakpoint-up(xl) {
				padding-top: 80px;
			}

			@include media-breakpoint-up(xxl) {
				padding-top: 120px;
			}
		}

		&__text-col {
			@include media-breakpoint-up(xl) {
				margin-left: 8.33333333% !important;
			}
		}

		&__text-content {
			margin-bottom: 40px;

			@include media-breakpoint-up(xl) {
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: flex-start;
				padding: 50px 0;
				margin-bottom: 0;
				min-height: 630px;
			}
		}

		&__img-holder {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 50vw;
		}

		&__img {
			border-top-right-radius: 20px;
		}

		&__bottom-img {
			@include responsiveRatio(375, 300);

			position: relative;

			@include media-breakpoint-up(sm) {
				@include responsiveRatio(1024, 450);
			}
		}
	}

	&-image-and-content {
		&.reversed {
			.section-image-and-content__row {
				@include media-breakpoint-up(lg) {
					flex-direction: row-reverse;
					justify-content: flex-end;
				}
			}

			.section-image-and-content__img-col {
				@include media-breakpoint-up(xl) {
					margin-left: 8.33333333%;
				}
			}

			.section-image-and-content__img-wrap {
				@include media-breakpoint-up(lg) {
					padding-right: 0;
					padding-left: 60px;
				}
			}
		}

		&__img-wrap {
			@include media-breakpoint-up(lg) {
				padding-right: 60px;
			}
		}

		&__img-col {
			@include media-breakpoint-down(lg) {
				margin: 0 auto;
			}
		}
	}
}

.section-full-width-img {
	@include responsiveRatio(1600, 625);

	position: relative;
	overflow: hidden;

	@include media-breakpoint-up(sm) {
		margin-bottom: 40px;
	}

	@include media-breakpoint-up(lg) {
		margin-bottom: 60px;
	}
}