.info-item {
	position: relative;
	padding-left: 36px;

	@include media-breakpoint-up(xxl) {
		padding-left: 52px;
	}

	@include media-breakpoint-up(lg) {
		max-width: 300px;
	}

	&__icon {
		@include sizeElement(24px);

		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0, -50%);
		z-index: 5;

		@include media-breakpoint-up(xxl) {
			@include sizeElement(32px);
		}
	}

	&__text {
		margin-bottom: 0;

		strong {
			font-size: 20px;
			font-weight: 700;

			@include media-breakpoint-up(xxl) {
				font-size: 24px;
			}
		}
	}
}