.dropdown {
	&-toggle {
		font-weight: 600;

		&:after {
			content: "\e906";
			display: inline-block;
			border: none;
			margin-left: 4px;
			font-family: icomoon;
			font-size: 16px;
			line-height: 1;
			font-weight: 600;
			transition: transform 0.3s;
		}

		&.show {
			&:after {
				transform: rotate(180deg);
			}
		}
	}

	&-menu {
		overflow: hidden;
		box-shadow: $dropdown-box-shadow;
	}

	&-item {
		font-weight: 600;
		transition: color 0.3s, background-color 0.3s;

		@include media-breakpoint-down(xxl) {
			font-size: 16px;
		}
	}

	&-lang-switcher {
		display: inline-flex;

		&__toggle {
			position: relative;
			display: inline-flex;
			align-items: center;
			min-height: 32px;
			padding: 4px 12px 4px 42px;
			font-size: 12px;
			color: $secondary;
			background-color: $white;
			border-radius: 8px;
			border: 1px solid rgba($secondary, 0.2);
			transition: background-color 0.3s;

			@include media-breakpoint-up(md) {
				font-size: 14px;
				padding: 6px 16px 6px 48px;
				min-height: 36px;
			}

			&:hover {
				background-color: rgba($secondary, 0.05);
			}

			&:after {
				content: "\e906";
				display: inline-block;
				border: none;
				margin-left: 4px;
				font-family: icomoon;
				font-size: 16px;
				line-height: 1;
				font-weight: 400;
				transition: transform 0.3s;
			}

			&.show {
				border-bottom-color: transparent;
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;

				&:after {
					transform: rotate(180deg);
				}
			}
		}

		&__flag {
			@include sizeElement(20px);

			position: absolute;
			top: 50%;
			left: 15px;
			transform: translate(0, -50%);
			z-index: 1;
			overflow: hidden;

			@include media-breakpoint-up(md) {
				left: 20px;
			}
		}

		&__menu {
			background-color: $white;
			border-radius: 0 0 8px 8px;
			border: 1px solid rgba($secondary, 0.2);
			border-top: none;
			padding: 0;
			min-width: 100%;
			width: 100%;
			margin: -1px 0 0 0 !important;
			overflow: hidden;
			box-shadow: none;
		}

		&__menu-link {
			position: relative;
			display: inline-flex;
			align-items: center;
			padding: 4px 12px 4px 42px;
			min-height: 32px;
			font-size: 12px;
			color: $secondary;

			@include media-breakpoint-up(md) {
				padding: 6px 16px 6px 48px;
				min-height: 36px;
				font-size: 14px;
			}

			&:hover {
				background-color: rgba($secondary, 0.05);
				color: $secondary;
			}
		}
	}
}