.parallax-img {
	@include responsiveRatio(639, 528);

	position: relative;
	max-width: 550px;
	margin: 0 auto;

	@include media-breakpoint-up(xxl) {
		max-width: 694px;
	}

	&__bg {
		@include responsiveRatio(416, 364);

		position: absolute;
		top: 12%;
		left: 11.26%;
		max-width: 65.1%;
		width: 100%;
	}

	&__small-icon-holder {
		@include responsiveRatio(1, 1);

		position: absolute;
		z-index: 5;

		&.left {
			width: 10.32%;
			top: 40%;
			left: 0;
		}

		&.bottom {
			width: 9%;
			bottom: 14%;
			right: 31%;
		}

		&.right {
			width: 8.45%;
			top: 12%;
			right: 0;
		}
	}

	&__round-img {
		@include responsiveRatio(1, 1);

		position: absolute;
		top: 0;
		left: 16%;
		z-index: 5;
		width: 22.5%;
	}

	&__card-img {
		position: absolute;

		&.first {
			@include responsiveRatio(208, 336);

			width: 32.55%;
			bottom: 0;
			left: 22%;
			z-index: 10;
		}

		&.second {
			@include responsiveRatio(232, 358);

			width: 36.3%;
			top: 0;
			right: 13%;
			z-index: 5;
		}
	}
}

.parallax-img-with-photo {
	@include responsiveRatio(694, 608);

	position: relative;
	max-width: 550px;
	margin: 0 auto;

	@include media-breakpoint-up(xxl) {
		max-width: 694px;
	}

	&__bg {
		@include responsiveRatio(416, 364);

		position: absolute;
		top: 3%;
		left: 0;
		max-width: 61.1%;
		width: 100%;
		z-index: 1;
	}

	&__photo {
		@include responsiveRatio(628, 507);

		position: absolute;
		top: 7%;
		right: 2%;
		max-width: 92.35%;
		width: 100%;
		z-index: 2;
	}

	&__bottom-badge {
		position: absolute;
		left: auto !important;
		top: auto !important;
		right: 2%;
		bottom: 6%;
		width: 29%;
		z-index: 3;
	}

	&__icon-holder {
		@include responsiveRatio(1, 1);

		position: absolute;
		z-index: 5;

		&.top-left {
			width: 7.82%;
			top: 9%;
			left: 13%;
		}

		&.top-right {
			width: 6.2%;
			top: 0;
			right: 0;
		}

		&.bottom {
			width: 6.2%;
			bottom: 0;
			left: 37%;
		}

		&.bottom-right {
			width: 6.2%;
			bottom: 5%;
			right: 20%;
		}
	}

	&__links {
		position: absolute;
		top: 50%;
		left: 14%;
		right: 10%;
		z-index: 10;
		transform: translate(0, -50%);
	}
}