.of-contain {
	@include absoluteCover;

	object-fit: contain;
}

.of-cover {
	@include absoluteCover;

	object-fit: cover;
}

.opacity-60 {
	opacity: 0.6 !important;
}

.opacity-80 {
	opacity: 0.8 !important;
}

.custom-scrollbar {
	@include customScrollbar(10px, $blue-dark, $primary, 10px);
}

.resize-animation-stopper * {
	animation: none !important;
	transition: none !important;
}

.out-of-container-on-mobile {
	@include media-breakpoint-down(sm) {
		margin: 0 (-$container-padding);
	}

	.card-round-icon-and-text {
		@include media-breakpoint-down(sm) {
			border: none;
			border-radius: 0;
			margin-bottom: 0;
		}
	}

	.card-why-us {
		@include media-breakpoint-down(sm) {
			border-radius: 0;
			margin-bottom: 0;
		}
	}
}

.bg-purple-faded {
	background-color: $purple-faded !important;
}

.bg-sm-purple-faded {
	@include media-breakpoint-up(sm) {
		background-color: $purple-faded !important;
	}
}

.pb-lg-0-df {
	@include media-breakpoint-down(lg) {
		padding-bottom: 0 !important;
	}
}

.rounded-top-sm-20 {
	@include media-breakpoint-up(sm) {
		border-top-left-radius: 20px !important;
		border-top-right-radius: 20px !important;
	}
}

.rounded-bottom-lg-20 {
	@include media-breakpoint-up(lg) {
		border-bottom-left-radius: 20px !important;
		border-bottom-right-radius: 20px !important;
	}
}

.rounded-sm-20 {
	@include media-breakpoint-up(sm) {
		border-radius: 20px !important;
	}
}

.row {
	&.gutter-md-32 {
		@include media-breakpoint-up(md) {
			@include addGridGutter(32, "md");
		}
	}

	&.gutter-xl-36 {
		@include media-breakpoint-up(xl) {
			@include addGridGutter(36, "xl");
		}
	}

	&.gutter-xl-60 {
		@include media-breakpoint-up(xl) {
			@include addGridGutter(60, "xl");
		}
	}

	&.gutter-xxl-40 {
		@include media-breakpoint-up(xxl) {
			@include addGridGutter(40, "xl");
		}
	}

	&.gutter-xxl-112 {
		@include media-breakpoint-up(xxl) {
			@include addGridGutter(112, "xl");
		}
	}
}