.tech-solution-item {
	@include responsiveRatio(375, 280);

	position: relative;
	display: block;
	overflow: hidden;

	@include media-breakpoint-up(sm) {
		border-radius: 20px;
		margin-bottom: 20px;
	}

	@include media-breakpoint-up(md) {
		@include responsiveRatio(944, 280);
	}

	&:hover {
		&:after {
			opacity: 0;
		}
	}

	&:after {
		@include absoluteCover;

		content: '';
		display: block;
		background-color: rgba($black, 0.5);
		transition: opacity 0.3s;
	}

	&__badge-holder {
		position: absolute;
		top: 20px;
		left: 20px;
		right: 20px;
		z-index: 5;
	}
}

.tech-solutions-list {
	@include media-breakpoint-up(xl) {
		margin: 0 (-$grid-gutter);
	}

	&:after {
		display: block;
		clear: both;

		@include media-breakpoint-up(xl) {
			content: "";
		}
	}

	&__column {
		@include media-breakpoint-up(xl) {
			width: 50%;
			padding: 0 $grid-gutter;
		}

		&:nth-child(2n + 1) {
			@include media-breakpoint-up(xl) {
				float: left;
			}
		}

		&:nth-child(2n) {
			@include media-breakpoint-up(xl) {
				float: right;
			}
		}

		&:nth-child(4n + 1),
		&:nth-child(4n + 4) {
			.tech-solution-item {
				@include media-breakpoint-up(xl) {
					@include responsiveRatio(680, 534);
				}
			}
		}

		&:nth-child(4n + 2),
		&:nth-child(4n + 3) {
			.tech-solution-item {
				@include media-breakpoint-up(xl) {
					@include responsiveRatio(680, 700);
				}
			}
		}
	}
}