.logos-list {
	@include listReset;

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	margin: 0 -15px;

	@include media-breakpoint-up(xxl) {
		margin: 0 -55px;
	}

	&__item {
		max-width: 100%;
		width: 33.33333%;
		padding: 20px 15px;
		flex: 0 0 auto;
		text-align: center;

		@include media-breakpoint-up(xs) {
			width: 25%;
		}

		@include media-breakpoint-up(sm) {
			width: 20%;
		}

		@include media-breakpoint-up(md) {
			padding: 35px 15px;
		}

		@include media-breakpoint-up(lg) {
			width: 14%;
		}
	}

	&__logo-holder {
		@include responsiveRatio(165, 60);

		position: relative;
		max-width: 165px;
		margin: 0 auto;
	}
}