.our-company-items-list {
	&__item {
		&:not(:last-child) {
			margin-bottom: 30px;

			@include media-breakpoint-up(lg) {
				margin-bottom: 20px;
			}
		}

		&:nth-child(2n) {
			.our-company-item__row {
				@include media-breakpoint-up(lg) {
					flex-direction: row-reverse;
				}
			}

			.our-company-item__text-col {
				@include media-breakpoint-up(lg) {
					margin-left: 0;
				}
			}

			.our-company-item__img-col {
				@include media-breakpoint-up(lg) {
					margin-left: 8.33333333%;
				}
			}
		}
	}
}

.our-company-item {
	&__img {
		@include responsiveRatio(375, 330);

		position: relative;
		overflow: hidden;
		margin-bottom: 30px;

		@include media-breakpoint-up(xs) {
			@include responsiveRatio(864, 300);

		}

		@include media-breakpoint-up(sm) {
			border-radius: 20px;
		}

		@include media-breakpoint-up(lg) {
			@include responsiveRatio(680, 600);

			margin-bottom: 0;
		}
	}

	&__text-col {
		@include media-breakpoint-up(lg) {
			margin-left: 8.33333333%;
		}
	}
}