body {
	@extend .custom-scrollbar;

	min-width: 320px;

	@include media-breakpoint-down(xxl) {
		font-size: 16px;
	}
}

.page-wrapper {
	overflow: hidden;
}

.main {
	> section {
		&:first-child {
			padding-top: 110px;

			@include media-breakpoint-up(md) {
				padding-top: 140px;
			}

			@include media-breakpoint-up(lg) {
				padding-top: 160px;
			}

			@include media-breakpoint-up(xxl) {
				padding-top: 210px;
			}
		}

		&:nth-last-child(2n) {
			&.section-our-team {
				+ .section-banner {
					@include media-breakpoint-down(sm) {
						margin-top: 0;
					}
				}
			}
		}

		&:last-child {
			&.section-banner {
				@include media-breakpoint-down(sm) {
					padding: 0;
					margin-top: -40px;
				}
			}

			&.section-content-and-side-img {
				padding-bottom: 0;
			}
		}
	}
}