.order-fulfillment-item {
	position: relative;
	display: block;
	overflow: hidden;

	@include media-breakpoint-down(sm) {
		@include responsiveRatio(375, 160);
	}

	@include media-breakpoint-up(sm) {
		border-radius: 20px;
		margin-bottom: 20px;
		height: 240px;
	}

	@include media-breakpoint-up(xxl) {
		height: 300px;
	}

	&:hover {
		.order-fulfillment-item__img {
			@include gray;
		}
	}

	&__img {
		transition: filter 0.3s;
	}

	&__icon {
		@include sizeElement(42px);
		@include inlineFlexCenter;

		position: absolute;
		bottom: 12px;
		left: 12px;
		z-index: 5;
		background-color: $white;
		border-radius: 50%;

		@include media-breakpoint-up(xs) {
			@include sizeElement(48px);
		}

		@include media-breakpoint-up(xxl) {
			@include sizeElement(60px);

			bottom: 20px;
			left: 20px;
		}
	}

	&__icon-holder {
		@include sizeElement(24px);

		position: relative;

		@include media-breakpoint-up(xxl) {
			@include sizeElement(30px);
		}
	}

	&__badge {
		position: absolute;
		bottom: 12px;
		left: 62px;
		max-width: calc(100% - 80px);
		overflow: hidden;
		white-space: nowrap !important;
		text-overflow: ellipsis;

		@include media-breakpoint-up(xs) {
			left: 68px;
		}

		@include media-breakpoint-up(xxl) {
			bottom: 20px;
			left: 88px;
			max-width: calc(100% - 108px);
		}
	}
}

.order-fulfillment-list {
	&__col {
		flex: 0 0 auto;
		width: 100%;

		@include media-breakpoint-up(md) {
			width: 50%;
		}

		@include media-breakpoint-up(lg) {
			width: 33.33333333%;
		}

		@include media-breakpoint-up(xl) {
			width: 25%;
		}

		&:nth-child(7n + 1) {
			@include media-breakpoint-up(md) {
				width: 50%;
			}

			@include media-breakpoint-up(xl) {
				width: 33.33333333%;
			}
		}

		&:nth-child(7n + 2) {
			@include media-breakpoint-up(md) {
				width: 50%;
			}

			@include media-breakpoint-up(xl) {
				width: 41.66666667%;
			}
		}

		&:nth-child(7n + 3) {
			@include media-breakpoint-down(xl) {
				width: 50%;
			}

			@include media-breakpoint-down(md) {
				width: 100%;
			}
		}

		&:nth-child(7n + 4) {
			@include media-breakpoint-down(xl) {
				width: 50%;
			}

			@include media-breakpoint-down(md) {
				width: 100%;
			}
		}
	}
}