.customer-service-item {
	@include responsiveRatio(375, 280);

	position: relative;
	display: block;
	overflow: hidden;

	@include media-breakpoint-up(sm) {
		@include responsiveRatio(1, 1);

		margin-bottom: 20px;
		border-radius: 20px;
	}

	&:hover {
		.badge {
			background-color: rgba($primary, 0.6) !important;
		}
	}

	&__badge-holder {
		position: absolute;
		left: 44px;
		bottom: 12px;
		right: 44px;
		z-index: 5;

		@include media-breakpoint-up(sm) {
			left: 8px;
			bottom: 8px;
			right: 8px;
		}

		@include media-breakpoint-up(lg) {
			left: 20px;
			bottom: 20px;
			right: 20px;
		}

		.badge {
			transition: background-color 0.3s;
		}
	}
}