.milestones-list {
	@include listReset;

	overflow: hidden;
	border-radius: 12px;

	@include media-breakpoint-up(sm) {
		border-radius: 20px;
	}

	&__item-list {
		@include descendingZindex;

		&:nth-child(6n + 1) {
			.milestones-list__item {
				background-color: $yellow;
			}
		}

		&:nth-child(6n + 2) {
			.milestones-list__item {
				background-color: $primary;
				color: $white;
			}

			&:before {
				background-color: $primary;
			}
		}

		&:nth-child(6n + 3) {
			.milestones-list__item {
				background-color: $blue;
				color: $white;
			}

			&:before {
				background-color: $blue;
			}
		}

		&:nth-child(6n + 4) {
			.milestones-list__item {
				background-color: $secondary;
				color: $white;
			}

			&:before {
				background-color: $secondary;
			}
		}

		&:nth-child(6n + 5) {
			.milestones-list__item {
				background-color: $gray-100;
			}

			&:before {
				background-color: $gray-100;
			}
		}

		&:nth-child(6n + 6) {
			.milestones-list__item {
				background-color: $primary;
				color: $white;
			}

			&:before {
				background-color: $primary;
			}
		}

		&:not(:first-child) {
			&:before {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: calc(100% + 50px);
				border-radius: 0 0 12px 12px;

				@include media-breakpoint-up(sm) {
					border-radius: 0 0 20px 20px;
				}
			}
		}
	}

	&__item {
		position: relative;
		padding: 24px $container-padding;
		border-radius: 12px;
		z-index: 5;

		@include media-breakpoint-up(sm) {
			padding: 25px 32px;
			border-radius: 20px;
		}

		@include media-breakpoint-up(lg) {
			padding: 32px 40px;
		}
	}
}