.badge {
	white-space: inherit;

	&.w-100 {
		justify-content: center;
	}

	&-lg {
		font-size: 14px;
		line-height: 1.2;
		border-radius: 0.5em;
		padding: 0.9002em 1.66666em;

		@include media-breakpoint-up(xs) {
			font-size: 16px;
		}

		@include media-breakpoint-up(xxl) {
			font-size: 20px;
		}
	}

	&.bg-blue-dark {
		background-color: rgba($blue-dark, 0.6) !important;
		backdrop-filter: blur(20px);
		-webkit-backdrop-filter: blur(20px);
	}
}