.interactive-link {
	position: relative;
	display: block;
	font-size: 12px;
	font-weight: $headings-font-weight;
	padding: 0.8666666667em 3.6666666667em;
	background-color: $white;
	color: $secondary;
	border-radius: 50rem;
	transition: color 0.3s, background-color 0.3s;

	@include media-breakpoint-up(xs) {
		font-size: 16px;
	}

	@include media-breakpoint-up(sm) {
		font-size: 18px;
	}

	@include media-breakpoint-up(md) {
		font-size: 22px;
	}

	@include media-breakpoint-up(xxl) {
		font-size: 30px;
	}

	&:hover {
		color: $white;
		background-color: $primary;

		.interactive-link__right-icon {
			animation: bounce 2s infinite cubic-bezier(0.280, 0.840, 0.420, 1);
		}
	}

	&__left-icon {
		@include sizeElement(2.3333333333em);

		position: absolute;
		top: 50%;
		left: 0.5333333333em;
		transform: translate(0, -50%);
	}

	&__right-icon {
		@include sizeElement(2.3333333333em);

		position: absolute;
		top: -0.4666666667em;
		right: 0.8em;
	}
}