.btn {
	@include inlineFlexCenter;

	box-shadow: none !important;

	// sizes
	&:not(.btn-sm):not(.btn-lg) {
		min-height: 44px;

		@include media-breakpoint-up(md) {
			min-height: 52px;
		}

		@include media-breakpoint-up(xxl) {
			min-height: 60px;
		}

		@include media-breakpoint-down(md) {
			font-size: 16px;
		}

		@include media-breakpoint-down(xxl) {
			font-size: 16px;
			padding-left: 30px;
			padding-right: 30px;
		}
	}

	&-sm {
		min-height: 44px;

		@include media-breakpoint-down(xxl) {
			font-size: 16px;
		}
	}

	// colors
	&-transparent-yellow {
		@include button-variant(
			transparent,
			transparent,
			$white,
			$yellow,
			$yellow,
			$blue-dark,
			$yellow,
			$yellow,
			$blue-dark
		);
	}

	&-outline-primary {
		color: $white;
	}

	// actions
	&.loading {
		&:after {
			@include sizeElement(30px);

			content: '';
			display: inline-block;
			border: 3px solid $secondary;
			border-top: 3px solid currentColor;
			margin-left: 15px;
			border-radius: 50%;
			animation: rotate 1s infinite linear;
		}
	}
}