.block-with-side-purple-cards {
	&:first-child {
		.block-with-side-purple-cards__col-cards {
			&:before {
				@include media-breakpoint-up(lg) {
					border-top-left-radius: 50px;
				}
			}
		}
	}

	&:last-child:nth-child(2n + 1) {
		.block-with-side-purple-cards__col-cards {
			&:before {
				@include media-breakpoint-up(lg) {
					border-bottom-left-radius: 50px;
				}
			}
		}
	}

	&:last-child:nth-child(2n) {
		.block-with-side-purple-cards__col-cards {
			&:before {
				@include media-breakpoint-up(lg) {
					border-bottom-right-radius: 50px;
				}
			}
		}
	}

	&:not(:last-child) {
		@include media-breakpoint-down(lg) {
			margin-bottom: 40px;
		}
	}

	&:nth-child(2n) {
		.block-with-side-purple-cards__row {
			flex-direction: row-reverse;
			justify-content: flex-end;
		}

		.block-with-side-purple-cards__col-text {
			@include media-breakpoint-up(lg) {
				margin-left: 8.33333333%;
			}
		}

		.block-with-side-purple-cards__col-cards {
			margin-left: 0;

			&:before {
				left: auto;
				right: -100vw;

				@include media-breakpoint-up(lg) {
					right: 0;
				}
			}
		}

		.block-with-side-purple-cards__cards {
			padding-left: 0;

			@include media-breakpoint-up(lg) {
				padding-right: 35px;
			}

			@include media-breakpoint-up(xl) {
				margin-right: 0;
				margin-left: -45px;
			}

			@include media-breakpoint-up(xxl) {
				padding-right: 65px;
			}
		}
	}

	&__col-text {
		margin-bottom: 40px;

		@include media-breakpoint-up(lg) {
			padding-top: 60px;
			padding-bottom: 60px;
		}

		@include media-breakpoint-up(xl) {
			padding-top: 80px;
			padding-bottom: 80px;
		}

		@include media-breakpoint-up(xxl) {
			padding-top: 110px;
			padding-bottom: 110px;
		}
	}

	&__col-cards {
		position: relative;
		display: flex;

		@include media-breakpoint-up(lg) {
			margin-left: 8.33333333%;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			bottom: 0;
			left: -100vw;
			width: 300vw;
			background-color: $primary;

			@include media-breakpoint-up(lg) {
				left: 0;
				width: 100vw;
			}
		}
	}

	&__cards {
		position: relative;
		z-index: 5;
		display: flex;
		align-items: center;
		padding: 50px 0 30px;

		@include media-breakpoint-up(lg) {
			padding-left: 35px;
		}

		@include media-breakpoint-up(xl) {
			margin-right: -45px;
			padding: 70px 0 35px 35px;
		}

		@include media-breakpoint-up(xxl) {
			padding: 105px 0 70px 65px;
		}
	}
}