.header {
	padding: 25px 0;
	transition: top 0.3s, padding 0.3s, background-color 0.3s;

	@include media-breakpoint-up(xxl) {
		top: 35px;
	}

	&.active {
		padding: 10px 0;
		background-color: $blue-dark;

		@include media-breakpoint-up(lg) {
			padding: 15px 0;
		}

		@include media-breakpoint-up(xxl) {
			top: 0;
		}

		.header__mobile-menu-close-btn {
			top: 16px;

			@include media-breakpoint-up(lg) {
				top: 24px;
			}
		}
	}

	&__main-content {
		position: relative;
		z-index: 5;
	}

	&__logo {
		display: flex;
		max-width: 48px;
		width: 100%;
		transform: translate(0, -3px);

		@include media-breakpoint-up(lg) {
			max-width: 72px;
			transform: translate(0, -5px);
		}
	}

	&__hamburger-menu {
		display: inline-flex;
		font-size: 32px;
		line-height: 1;
		color: $white;

		@include media-breakpoint-up(lg) {
			font-size: 48px;
		}
	}

	&__mobile-menu {
		@extend .custom-scrollbar;

		@include media-breakpoint-down(xl) {
			position: fixed;
			top: 0;
			left: 0;
			bottom: 0;
			width: 100vw;
			z-index: $zindex-fixed;
			background-color: $blue-dark;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.3s, visibility 0.3s;
			overflow: auto;
		}

		&.active {
			@include media-breakpoint-down(xl) {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__mobile-menu-container {
		@extend .container;

		@include media-breakpoint-down(xl) {
			position: relative;
			padding-top: 80px;
			padding-bottom: 80px;
		}

		@include media-breakpoint-up(xl) {
			max-width: 100%;
			padding: 0;
		}
	}

	&__mobile-menu-close-btn {
		@include sizeElement(32px);
		@include inlineFlexCenter;

		position: absolute;
		top: 30px;
		right: $container-padding;
		z-index: 5;
		display: inline-flex;
		font-size: 32px;
		padding: 0;
		border: none;
		line-height: 1;
		border-radius: 0;
		background-color: transparent;
		color: rgba($white, 0.4);
		transition: color 0.3s;

		&:hover {
			color: $white;
		}

		@include media-breakpoint-up(lg) {
			@include sizeElement(48px);

			top: 34px;
		}

		@include media-breakpoint-up(xl) {
			display: none;
		}
	}
}

.header-menu {
	@include listReset;

	@include media-breakpoint-up(xl) {
		display: flex;
		align-items: center;
	}

	&__item {
		@include media-breakpoint-up(xl) {
			margin-top: 5px;
			margin-bottom: 5px;
		}

		&:not(:first-child) {
			@include media-breakpoint-up(xl) {
				margin-left: 15px;
			}
		}

		&:not(:last-child) {
			@include media-breakpoint-down(xl) {
				margin-bottom: 25px;
			}
		}

		.btn {
			@include media-breakpoint-down(xl) {
				padding: 0;
				border-radius: 0;
				min-height: auto;
				border: none;
			}

			&:hover {
				@include media-breakpoint-down(xl) {
					background-color: transparent;
					color: $yellow;
				}
			}

			&[href^="tel:"],
			&[href^="mailto:"],
			&[href^="https://www.linkedin.com"] {
				font-weight: 500;

				&:before {
					display: inline-block;
					font-family: icomoon;
					color: $yellow;
					font-size: 1.333em;
					min-width: 1em;
					margin-right: 5px;
					transition: color 0.3s;
				}

				&:hover {
					&:before {
						color: $primary;
					}
				}
			}

			&[href^="tel:"] {
				&:before {
					content: '\e90f';
				}
			}

			&[href^="mailto:"] {
				&:before {
					content: '\e903';
				}
			}

			&[href^="https://www.linkedin.com"] {
				&:before {
					content: '\e902';
				}
			}
		}

		.dropdown {
			@include media-breakpoint-down(xl) {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
			}

			&-toggle {
				&.show {
					@include media-breakpoint-down(xl) {
						background-color: transparent;
						color: $yellow;
					}
				}
			}

			&-menu {
				@include media-breakpoint-down(xl) {
					position: static !important;
					transform: none !important;
					background-color: rgba($white, 0.03);
					min-width: 220px;
					margin-top: 25px !important;
				}

				> li {
					margin: 0 !important;
				}
			}

			&-item {
				@include media-breakpoint-down(xl) {
					color: $white;
				}

				&:hover {
					@include media-breakpoint-down(xl) {
						background-color: $primary;
					}
				}
			}
		}
	}
}