.contact-item {
	position: relative;
	padding-left: 65px;
	color: $white;

	@include media-breakpoint-up(xxl) {
		padding-left: 75px;
	}

	&__icon {
		@include inlineFlexCenter;
		@include sizeElement(1.6em);

		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0, -50%);
		z-index: 5;
		border-radius: 50%;
		background-color: $primary;
		color: $white;
		font-size: 30px;
		transition: background-color 0.3s;

		&:hover {
			background-color: darken($primary, 10%);
		}

		.icomoon-logo {
			margin-top: -2px;
		}
	}

	&__link {
		color: $white;
		transition: color 0.3s;

		&:hover {
			color: $yellow;
		}
	}
}