.banner-block {
	position: relative;
	padding: 60px $container-padding;
	background-color: $purple-dark;
	color: $white;
	overflow: hidden;

	@include media-breakpoint-up(sm) {
		border-radius: 20px;
		padding: 40px;
	}

	@include media-breakpoint-up(md) {
		display: flex;
		align-items: center;
		min-height: 315px;
		border-radius: 40px;
	}

	@include media-breakpoint-up(xl) {
		min-height: 375px;
	}

	@include media-breakpoint-up(xxl) {
		min-height: 450px;
	}

	&__bg {
		position: absolute;
		z-index: 5;

		@include media-breakpoint-down(sm) {
			display: none;
		}

		&.left {
			height: 90%;
			bottom: 0;
			left: 0;
		}

		&.right {
			height: 90%;
			top: 0;
			right: 0;
		}
	}

	&__content {
		position: relative;
		z-index: 5;
	}
}