.footer {
	border-top: 1px solid rgba($gray-600, 0.3);

	&__top-content-column {
		padding-top: 30px;
		padding-bottom: 10px;

		@include media-breakpoint-up(xl) {
			padding-top: 80px;
			padding-bottom: 35px;
		}

		&:not(:last-child) {
			@include media-breakpoint-up(lg) {
				border-right: 1px solid rgba($gray-600, 0.3);
			}
		}
	}

	&__bottom-content {
		padding: 20px 0;

		@include media-breakpoint-up(sm) {
			border-top: 1px solid rgba($gray-600, 0.3);
		}

		@include media-breakpoint-up(xl) {
			padding: 30px 0;
		}
	}

	&__logo {
		display: flex;
		max-width: 48px;
		width: 100%;

		@include media-breakpoint-up(md) {
			max-width: 72px;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}
}

.footer-nav {
	@include listReset;

	font-size: 12px;

	@include media-breakpoint-up(sm) {
		font-size: $font-size-xs;
	}

	&__item {
		&:not(:last-child) {
			margin-bottom: 8px;

			@include media-breakpoint-up(xxl) {
				margin-bottom: 16px;
			}
		}
	}

	&__link {
		color: $secondary;
		transition: color 0.3s;

		&:hover {
			color: $primary;
		}
	}
}