a {
	&.text-primary {
		transition: color 0.3s;

		&:hover {
			color: lighten($primary, 15%) !important;
		}
	}

	&.with-underline {
		border-bottom: 1px solid currentColor;
	}
}

.link-with-icon {
	position: relative;
	display: inline-flex;
	font-size: 12px;
	color: $secondary;
	padding-left: 28px;
	transition: color 0.3s;

	@include media-breakpoint-up(sm) {
		font-size: $font-size-xs;
		padding-left: 32px;
	}

	&:hover {
		color: $primary;
	}

	&__icon {
		position: absolute;
		top: -2px;
		left: 0;
		font-size: 20px;
		color: $primary;

		@include media-breakpoint-up(sm) {
			top: 0;
		}
	}
}