@import "forms/select2";
@import "forms/cf7";
@import "forms/custom-checkbox";
@import "forms/newsletter-form";

.form-control {
	caret-color: $input-color;

	&[disabled] {
		-webkit-text-fill-color: $input-disabled-color;
	}

	&.is-invalid {
		box-shadow: none !important;

		&:focus {
			border-color: $input-focus-border-color;
		}
	}

	@include media-breakpoint-down(xxl) {
		font-size: 16px;
		padding: 12px 16px;
		border-radius: 8px;
	}
}

.bg-blue-dark {
	.form-control {
		@include placeholder {
			color: $white;
		};

		border: 1px solid rgba($white, 0.2);
		background-color: rgba($white, 0.03);
		color: $white;

		&:focus {
			border-color: rgba($white, 0.5);
		}
	}

	.select2-container {
		&--bootstrap {
			&.select2-container--focus {
				.select2-selection {
					border-color: rgba($white, 0.5);
				}
			}

			.select2-selection {
				border: 1px solid rgba($white, 0.2);
				background-color: rgba($white, 0.03);
				color: $white;

				&__placeholder {
					color: $white;
				}

				&__arrow {
					background-image: escape-svg($form-select-indicator-white);
				}
			}

			.select2-dropdown {
				background-color: $blue-dark;
				border-color: rgba($white, 0.5);

				.select2-results {
					&__option {
						color: $white;

						&:hover {
							color: $primary;
						}
					}
				}
			}
		}
	}

	.custom-checkbox {
		&:hover {
			.custom-checkbox__input:not(:disabled) ~ {
				.custom-checkbox__checkmark {
					background-color: darken($primary, 5%);
				}
			}
		}

		&__input {
			&:checked ~ {
				.custom-checkbox__checkmark {
					background-color: $primary;
				}
			}
		}

		&__text {
			color: $white;

			a {
				&:hover {
					color: $yellow;
				}
			}
		}

		&__checkmark {
			background-color: $primary;
		}

		&__icon {
			color: $white;
		}
	}
}

textarea {
	&.form-control {
		height: 150px;

		@include media-breakpoint-up(xxl) {
			height: 215px;
		}
	}
}

.invalid-feedback {
	font-size: 12px;
	font-weight: $font-weight-base;

	@include media-breakpoint-up(xxl) {
		font-size: $font-size-xs;
	}
}

.position-relative {
	.invalid-feedback {
		margin-top: 0;
		position: absolute;
		top: calc(100% + 4px);
		left: 12px;

		@include media-breakpoint-up(xxl) {
			top: calc(100% + 8px);
		}
	}
}