.profile-item {
	display: block;

	&__img-col,
	&__content-col {
		flex: 0 0 auto;
	}

	&__img {
		position: relative;
		width: 100%;
		min-height: 165px;
		overflow: hidden;

		@include media-breakpoint-up(sm) {
			border-radius: 30px;
			margin-bottom: 20px;
		}

		@include media-breakpoint-up(md) {
			min-height: 300px;
			margin-bottom: 0;
		}
	}

	.card-round-icon-and-text {
		display: flex;
		flex-direction: column;
		justify-content: center;
		min-height: 220px;
		margin-bottom: 0;

		@include media-breakpoint-up(md) {
			min-height: 300px;
		}
	}
}

.profile-list {
	.profile-item {
		&:not(:last-child) {
			@include media-breakpoint-up(sm) {
				margin-bottom: 20px;
			}
		}

		&:nth-child(2n) {
			.profile-item__row {
				@include media-breakpoint-up(md) {
					flex-direction: row-reverse;
				}
			}
		}

		&:nth-child(4n + 1),
		&:nth-child(4n + 4) {
			.profile-item__img-col {
				width: 100%;

				@include media-breakpoint-up(md) {
					width: 50%;
				}
			}

			.profile-item__content-col {
				width: 100%;

				@include media-breakpoint-up(md) {
					width: 50%;
				}
			}
		}

		&:nth-child(4n + 2) {
			.profile-item__img-col {
				width: 100%;

				@include media-breakpoint-up(md) {
					width: 50%;
				}

				@include media-breakpoint-up(xl) {
					width: 41.66666667%;
				}
			}

			.profile-item__content-col {
				width: 100%;

				@include media-breakpoint-up(md) {
					width: 50%;
				}

				@include media-breakpoint-up(xl) {
					width: 58.33333333%;
				}
			}
		}

		&:nth-child(4n + 3) {
			.profile-item__img-col {
				width: 100%;

				@include media-breakpoint-up(md) {
					width: 50%;
				}

				@include media-breakpoint-up(xl) {
					width: 41.66666667%;
				}
			}

			.profile-item__content-col {
				width: 100%;

				@include media-breakpoint-up(md) {
					width: 50%;
				}

				@include media-breakpoint-up(xl) {
					width: 58.33333333%;
				}
			}
		}
	}
}