.newsletter-form {
	position: relative;
	display: block;
	width: 100%;

	input {
		@include placeholder {
			color: $secondary;
		};

		display: block;
		width: 100%;
		height: 48px;
		font-size: 14px;
		border: 1px solid $secondary;
		background-color: $white;
		border-radius: 50rem;
		padding: 10px 56px 10px 20px;
		color: $secondary;
		text-overflow: ellipsis;
		caret-color: $secondary;
		transition: border-color 0.3s, background-color 0.3s;

		+ .wpcf7-not-valid-tip {
			display: none;
		}

		&.wpcf7-not-valid {
			border-color: $danger;
			background-color: rgba($danger, 0.03);
		}
	}

	button {
		@include sizeElement(32px);
		@include inlineFlexCenter;

		position: absolute;
		top: 50%;
		right: 8px;
		transform: translate(0, -50%);
		padding: 4px;
		border-radius: 50%;
		border: none;
		background-color: $secondary;
		color: $white;
		font-size: 24px;
		line-height: 1;
		transition: background-color 0.3s;

		&:hover {
			background-color: $primary;
		}
	}

	.wpcf7-form {
		&.init,
		&.sent {
			.wpcf7-response-output {
				color: $success;
			}
		}

		&.failed,
		&.aborted {
			.wpcf7-response-output {
				color: $danger;
			}
		}

		&.invalid,
		&.unaccepted,
		&.payment-required {
			.wpcf7-response-output {
				color: $danger;
			}
		}

		.wpcf7-response-output {
			font-size: $form-feedback-font-size;
			padding: 0px !important;
			margin-top: $form-feedback-margin-top !important;
			border: none !important;
			display: block !important;
		}
	}
}