$checkbox-checkmark-size: 32px;
$checkbox-bg-color: $pink-faded;

// states
$checkbox-checked-bg-color: $checkbox-bg-color;
$checkbox-disabled-bg-color: #a2a3b4;

.custom-checkbox {
	position: relative;
	padding-left: 48px;
	line-height: $checkbox-checkmark-size;
	cursor: pointer;

	@include media-breakpoint-down(xxl) {
		padding-left: 36px;
		line-height: 24px;
	}

	&__text {
		font-size: 18px;

		@include media-breakpoint-down(xxl) {
			font-size: 16px;
		}

		a {
			color: inherit;
			border-bottom: 1px solid currentColor;
			transition: color 0.3s, border-color 0.3s;

			&:hover {
				color: $primary;
			}
		}
	}

	&__checkmark {
		@include sizeElement($checkbox-checkmark-size);

		position: absolute;
		top: 0;
		left: 0;
		background-color: $checkbox-bg-color;
		border-radius: 4px;
		transition: background-color 0.3s, box-shadow 0.3s;

		@include media-breakpoint-down(xxl) {
			@include sizeElement(24px);
		}
	}

	&__icon {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: inline-block;
		font-size: 20px;
		color: $primary;
		opacity: 0;
		transition: opacity 0.2s;

		@include media-breakpoint-down(xxl) {
			font-size: 12px;
		}
	}

	&__input {
		@include visuallyHidden;

		// checked styles
		&:checked ~ {
			.custom-checkbox__checkmark {
				background-color: $checkbox-checked-bg-color;

				.custom-checkbox__icon {
					opacity: 1;
				}
			}
		}

		// disabled styles
		&:disabled ~ {
			.custom-checkbox__checkmark {
				background-color: $checkbox-disabled-bg-color;
			}
		}
	}

	&:hover {
		.custom-checkbox__input:not(:disabled) ~ {
			.custom-checkbox__checkmark {
				background-color: lighten($checkbox-bg-color, 5%);
			}
		}
	}
}