.oval-icon-and-text-item {
	display: flex;
	align-items: center;
	flex-wrap: nowrap;
	border-radius: 50rem;
	background-color: $yellow;
	color: $blue-dark;
	padding: 10px 20px;
	transition: background-color 0.3s, color 0.3s;

	@include media-breakpoint-up(lg) {
		padding: 18px 36px;
	}

	&:hover {
		background-color: $primary;
		color: $white;
	}

	&__icon {
		@include inlineFlexCenter;
		@include sizeElement(60px);

		border-radius: 50%;
		background-color: $white;
		position: relative;
		margin-right: 20px;
	}

	&__icon-holder {
		@include sizeElement(30px);

		position: relative;
	}
}