.youtube-thumbnail {
	position: relative;
	cursor: pointer;
	background-color: $black;
	border-radius: 12px;
	overflow: hidden;

	@include media-breakpoint-up(sm) {
		border-radius: 20px;
	}

	&.ratio-2dot6x1 {
		@include responsiveRatio(1340, 508);
	}

	&:hover {
		.youtube-thumbnail__icon {
			transform: translate(-50%, -50%) scale(1.25)
		}
	}

	&__media {
		@include absoluteCover;
		@include bgCover;

		z-index: 1;
	}

	&__icon {
		@include absoluteCenter;
		@include sizeElement(48px);
		@include inlineFlexCenter;

		border-radius: 50%;
		background-color: $yellow;
		color: $primary;
		transition: transform 0.3s;

		&:after {
			content: '\e90d';
			font-family: icomoon;
			display: inline-block;
			font-size: 12px;
			margin-right: -2px;
		}
	}
}